.note {
  &:first-child {
    margin-left: 0;
  }
  margin: 24px;
  height: 310px;
  width: 320px;
  border-radius: 24px;
  padding: 28px;
  background-color: #ffcf7d;
  position: relative;

  textarea {
    background: none;
    border: none;
    font-size: 21px;
    font-weight: 600;
    padding-right: 24px;
    outline: none;
    font-family: "Avenir", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    resize: none;
    height: 100%;

    &::placeholder {
      color: black;
    }
  }

  .footer {
    padding: 28px;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .date {
      span {
        font-size: 16px;
        font-weight: 500;
      }
    }
    .edit {
      button {
        border-radius: 100%;
        background-color: black;
        border: none;
        height: 48px;
        width: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        outline: none;
        cursor: pointer;

        img {
          height: 18px;
        }
      }
    }
  }
}
